import React, {Component} from 'react'

class Prices extends Component{
    state = {
        currency: 'USD'
    }

    render(){
        return (
            <div>
                <ul>
                    <li>Currency: {this.props.bpi.USD.description}</li>
                    <li>Rate: {parseFloat(this.props.bpi.USD.rate).toFixed(2)}</li>
                </ul>
            </div>
        )
    }
}

export default Prices